export const initialEventApp = (
    foreignplacement: Placement | null = null) => async (
        dispatch: AppDispatch, getState: AppGetState
    ) => {
        // dispatch(setPreloader(true))
        let placementdata = foreignplacement as Placement | null
        if (!placementdata && __IN_BITRIX__) {
            placementdata = await bitrixAPI.getPlacement() as Placement | null
        }
        const eventAC = eventActions
        dispatch(eventAC.setFinishStatus({ status: false, result: '' }))
        let placement = null
        if (placementdata) {
            //@ts-ignore
            if (placementdata.placement) {

                placement = placementdata
                console.log('initialCalling calling placement')
                console.log(placement)
            }
        }

        // dispatch(getCallingPlan())
        //@ts-ignore
        if (placementdata && placementdata.options) {
            //@ts-ignore
            dispatch(getBitrixItems(placementdata))
        } else {

            dispatch(
                eventAC
                    .setFullCompleteStatus({ status: true }))
        }


        // const departamentIds = [] as Array<string | number>
        // if (departament) {

        //     departament.map(user => departamentIds.push(user.ID))


        // }
        // if (departament) {
        //     dispatch(callingAC.setFetchedDepartament(departament))
        // }
        // dispatch(setPreloader(false))

        const initializedState = getState()


    }



